

.containerPartners{
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    @media screen and (min-width:480px){
        width: 480px;
    }
    @media screen and (min-width:768px){
        width: 768px;
        margin: 0 auto;
        padding-left: 100px;
        padding-right: 100px;
    }
    @media screen and (min-width: 1200px){
        width:1440px;
        margin: 0 auto;
        padding-left: 100px;
        padding-right: 86px;
    }
}
.titlePartners{
    display: flex;
    justify-content: center;
    font-family: $firstBold,sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.21;
    color:$text-color;
    margin-bottom: 50px;
}
.par_list{
    display: flex;
    flex-wrap: wrap;
    @media screen and (min-width:320px) and (max-width:479px){
        justify-content: center;
    }
}
.par_item{
    border: 1px solid #CDE6FF;
    align-items: center;
    display: flex;
    justify-content: center;
}
.par_link{
    width: 207px;
    height: 207px;
    display: flex;
    align-items: center;
    justify-content: center;
}